body {
  margin: 0;
  background-color: #202020;
  background-image: url('./backgrounds/concreteWall.png');
  background-repeat: repeat;
  font-family: BalooDa2-SemiBold, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'BalooDa2-SemiBold';
  src: local('BalooDa2-SemiBold'), url(./fonts/BalooDa2-SemiBold.ttf) format('ttf');
}